<template>
  <div>
    <CContainer>
      <CCard class="my-4">
        <CCardHeader>
          <div class="mb-3 p-3 bg-white text-center ">
            <h3 class="pt-3 mb-2">請確認您的訂單</h3>
            <small class="text-danger">※如需更改或取消請來電 <a href="tel:+886-89-325261">089-325261</a></small>
          </div>
        </CCardHeader>
      </CCard>

      <div class="text-center"> 共 <span>{{ orders.length }}</span> 筆</div>

      <CCard class="my-4" v-for="(order, orderIndex) in orders" :key="orderIndex">
        <CCardHeader class="d-flex align-items-center">
          <h4>訂單編號 </h4><h1 class="ml-3">{{ order.no }}</h1>
        </CCardHeader>
        <CCardBody>
          <div>取餐時間：<span>{{ order.order_time }}</span></div>
          <div>訂購姓名：{{ order.name }}</div>
          <div>聯絡電話：{{ order.phone }}</div>
          <hr>
          <CDataTable
            border
            :items="order.detail"
            :fields="fields"
          >
          </CDataTable>
          <div class="p-2 mb-2 bg-warning text-white text-center">總計 <span>{{ order.count }}</span> 樣菜 / 共 <span>${{ order.sum }}</span></div>
          <hr>
          <div>備註：</div>
          <h3 v-html="order.note"></h3>
        </CCardBody>
      </CCard>
    </CContainer>

    <Loading :isLoading="isLoading"/>
  </div>
</template>

<script>
import moment from 'moment'
import Loading from '@/views/Loading'

const { getSettingDoc } = require('@/gapi/getSettingDoc');
const { getOrderDoc } = require('@/gapi/getOrderDoc');
const fields = [{
  key: 'title',
  label: '餐點',
}, {
  key: 'number',
  label: '數量',
}, {
  key: 'sum',
  label: '小計',
}];

export default {
  name: 'History',
  data () {
    return {
      isLoading: true,
      sheetSettings: {},
      orderSheet: null,
      //訂單紀錄
      orders: [],
      //標頭
      fields,
    }
  },
  components: {
    Loading,
  },
  created: function(){
    this.accessSpreadSheet();
  },
  methods: {
    async accessSpreadSheet() {
      const adminDoc = await getSettingDoc();
      const orderDoc = await getOrderDoc();
      const formListSheet = adminDoc.sheetsByTitle['表單列表'];
      const formListRows = await formListSheet.getRows();
      
      //表單設定
      for (let index = 0; index < formListRows.length; index++) {
        const formRow = formListRows[index];
        
        if (moment().isAfter(formRow.start_time) && moment().isBefore(formRow.end_time)) {
          this.sheetSettings = formRow;
          this.orderSheet = orderDoc.sheetsByTitle[formRow.sheetTitle];
          break;
        }
      }

      const sheet = orderDoc.sheetsByTitle[this.sheetSettings.sheetTitle];
      const sheetRows = await sheet.getRows();
      for (let index = 0; index < sheetRows.length; index++) {
        const _record = sheetRows[index];
        const _cookie_phone = this.$cookies.get('phone');
        
        if (_record['phone'] == _cookie_phone) {
          _record.detail = JSON.parse(_record.detail);
          _record.no = index + 1;
          this.orders.push(_record);
        }
      }
      //停止loading
      this.isLoading = false;
    }
  }
}
</script>
